*{
  margin:0;
  padding: 0;
  box-sizing: border-box;

}
body{
  font-family: 'montserrat',sans-serif;
  background: #134e5e; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #134e5e, #71b280); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #134e5e, #71b280); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ 
}
header{
padding: 50px 15px 0px;
}
header h1 {
  color:#ffffff;
  font-size: 2em;
  font-weight: 400;
  text-align: center;
}

main{
  width:100%;
  max-width:960px;
  margin: 0 auto;

}
section{
  padding:15px;

}
.searchbox{
  display: block;
  width:100%;
  padding:15px;
  border:none;
  outline:none;
  background:none;
  background-color:#fff;
  border-radius: 8px;
  color:#53565A;
  font-size: 1.2em;
  font-weight:300;
  transition: 0.4s ease-out;
}
.searchbox:focus{
  box-shadow: 0px 0px 8px #ffffff;
}

.results{
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
}

.results .result{
  width: 33.3333%;
  min-width:250px;
  padding:15px;
  display:flex;
  flex-direction:column;
  cursor: pointer;
}
.results .result img{
  display: block;
  width: 100%;
}

.results .result  h3{
  color: #fff;
  font-size: 20px;
  font-weight: 600px;
  padding: 10px;
  flex: 1 1 100%;
  transition: 0.4s ease-out;
  background:#445565;
}

.results .result:hover h3{
  background: #fff;
  color: #223343;
}

.popup{
  position: fixed;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #445565;
  overflow-y: scroll;
}

.popup .content{
  display: block;
  width: 100%;
  max-width:960px;
  margin: 0 auto;
  color: #fff;
}
.popup .content h2{
  font-size: 2em;
  font-weight:600
}
.popup .content h2 span{
  font-weight: 300;
  font-size: 1.2em;
}

.popup .content .plot{
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px 30px;

}

.popup .content .rating{
  margin-bottom: 30px;
}

.popup .content .plot img{
  flex: 1 1 50%;
  max-width: 300px;
  padding: 0px 15px;
}

.popup .content .plot p{
  flex: 1 1 50%;
  pad: 15px;
}

.popup .content .close{
  display: inline-block;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: 800;
  background-color: #4484c4;
  color: #fff;
   border-radius: 8px;
   border: none;
   outline: none;
   appearance: none;
   cursor: pointer;
   transition: 0.4s ease-out;
}

.popup .content .close:hover{
  background:#222343;
}